	/*
	*注意数据格式
	* [{
            "id":2,
            "dictKey":"insurance_select",
            "dictValue":"是否保价",
            "dictParent":"selector",
            "seq":null,
            "parentId":1,
            "children":[
                {
                    "id":3,
                    "dictKey":"0",
                    "dictValue":"是",
                    "dictParent":"insurance_select",
                    "seq":1,
                    "parentId":2,
                    "children":[

                    ]
                },
                {
                    "id":4,
                    "dictKey":"1",
                    "dictValue":"否",
                    "dictParent":"insurance_select",
                    "seq":2,
                    "parentId":2,
                    "children":[

                    ]
                }
            ]
        }]
	* 
