	import customPicker from "@/components/custom-picker/custom-picker.vue"
	import customDate from "@/components/custom-date/custom-date.vue"
	export default {
		components:{
			customPicker,
			customDate
		},
		props:{
			width:{
				type:Number,
				default:670
			},
			height:{
				type:Number,
				default:40
			},
			inputWidth:{
				type:Number,
				default:40
			},
			titleWidth:{
				type:Number,
				default:220
			},
			title:{
				type:String,
				default:"当前选择"
			},
			list:{
				type:Array,
				default:undefined
			},
			bottomLine:{
				type:Boolean,
				default:false
			},
			iconflag:{
				type:Boolean,
				default:false
			},
			type:{
				type:String,
				default:'select'
			},
			placeholderText:{
				type:String,
				defalt:''
			},
			defaultvalue:{
				type:Number,
				defalt:0
			},
			defaulttext:{
				type:String,
				defalt:''
			},
			defaultdate:{
				type:String,
				defalt:''
			},
			disabled:{
				type:Boolean,
				default:false
			},
			themeColor:{
				type:String,
				default:'#009245'
			},
			textAlign:{
				type:String,
				default:'right'
			},
			datetype:{
				type:String,
				default:'day'
			},
			required:{
				type:Boolean,
				default:false
			}
			
		},
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				defaultDate:this.defaultdate,
				defaultValue:this.defaultvalue,
				defaultText:this.defaulttext,
				date: currentDate,
			};
		},
		
		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			}
		},
		methods:{
			onSelectHandle:function(e){
				this.$refs.custompicker.show(this.defaultValue)
			},
			onConfirmHandle:function(e){
				if(this.type === "select"){
					this.defaultValue = e.index
					this.$emit("selectId",e.index);
				}else if(this.type === 'date'){
					this.defaultDate = e.date
				}else if(this.type === 'startDate'){
					this.defaultDate = e.date
				}
			},
			onCancelHandle:function(){
				this.$refs.custompicker.hide()
			},
			onChangeHandle:function(e){
				console.log(e)
			},
			bindPickerChangeHandle:function(event){
				this.index = event.detail.value
			},
			inputHandle:function(e){
				this.defaultText = e.target.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			verification: function(e){
				var val = e
				var ranges = [
					'\ud83c[\udf00-\udfff]', 
					'\ud83d[\udc00-\ude4f]', 
					'\ud83d[\ude80-\udeff]'
				];
				return val.replace(new RegExp(ranges.join('|'), 'g'), '');
			},
		},
		watch: {
		  defaultText(val) {
			this.$nextTick(() => {
			  this.defaultText = this.verification(val)
			})
		  }
		}
	}
